.portfolio {
  padding: 12rem 0;
  background: var(--grey300);

  &__title {
    margin-bottom: 2.6rem;
  }

  &__control {
    margin-bottom: 2rem;
    align-items: center;
  }

  &__tabs {
    --offset: 2.6rem;
    display: flex;
    gap: 8rem;
    padding-bottom: var(--offset);
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    margin-bottom: var(--offset);

    @include mobile {
      display: none;
    }
  }

  &__content {
    display: none;

    &.active {
      display: block;
    }
  }

  &__tab {
    cursor: pointer;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 1.6rem;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--grey1000);
    position: relative;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      top: calc(100% + var(--offset));
      height: 3px;
      width: 100%;
      background-color: currentColor;
      opacity: 0;
      transform: scale(0) translateY(-50%);

      transition: .4s ease-out;
    }

    &.active {
      color: var(--red1000);

      &::after {
        opacity: 1;
        transform: scale(1) translateY(-50%);
      }
    }

    @include hover {
      color: var(--red1000);

      &::after {
        opacity: 1;
        transform: scale(1) translateY(-50%);
      }
    }
  }

  &__work {
    display: grid;
    grid-template-rows: 37rem 33.3rem;
    grid-auto-rows: 33.3rem;
    gap: 2rem;





    grid-template-areas: "a a b b b c c"
      "d d d e e f f";

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: unset;
      grid-auto-rows: unset;
      grid-template-areas: "b b"
        "a a"
        "c d"
        "e e";
    }
  }

  &__name {
    display: none;



    font-weight: 400;
    font-size: 1.6rem;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: var(--grey1000);

    transition: opacity .3s ease-out;

    @include small-tablet {
      display: block;
    }
  }
}

.p-work__item {
  &:nth-child(6n + 1) {
    grid-area: a;
  }

  &:nth-child(6n + 2) {
    grid-area: b;
  }

  &:nth-child(6n+3) {
    grid-area: c;
  }

  &:nth-child(6n+4) {
    grid-area: d;
  }

  &:nth-child(6n+5) {
    grid-area: e;
  }

  &:nth-child(6n+6) {
    grid-area: f;

    @include small-tablet {
      display: none;
    }
  }
}

.p-item {
  background: var(--grey400);
  position: relative;

  @include small-tablet {
    max-height: 34rem;
    background: none;
  }

  @include mobile {
    max-height: 24rem;
  }

  &__body {
    height: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;
    padding: 3rem;
    padding-top: 4rem;

    @include small-tablet {
      padding: 2rem;
      gap: 2rem;
      align-items: center;
      text-align: center;
    }
  }

  &__capture {
    display: block;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 1.6rem;
    color: var(--grey1000);

    &:not(:last-child) {
      margin-bottom: 2rem;
    }

    span {
      font-weight: 500;
    }
  }

  &__img {
    height: 100%;
    width: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }
}