  margin-bottom: 3rem;
}
.sh h3 {
  margin: 0;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 2.4rem;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--grey1000);
}
.sh h3:not(:last-child) {
  margin-bottom: 3rem;
}
.sh h4 {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 24px;
  color: var(--grey1000);
}
.sh h4:not(:last-child) {
  margin-bottom: 2rem;
}
.sh p,
.sh ul,
.sh ol {
  margin: 0;
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1.6rem;
  color: var(--grey1000);
}
.sh p:not(:last-child),
.sh ul:not(:last-child),
.sh ol:not(:last-child) {
  margin-bottom: 3rem;
}
.sh ul,
.sh ol {
  margin: 1rem 0;
}
.sh .row {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  min-height: 34rem;
}
.sh .row:not(:last-child) {
  margin-bottom: 3rem;
}
.sh .row .right {
  position: relative;
}
.sh .row .right img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.sh .big-img {
  width: 100%;
  height: auto;
}
.sh .big-img:not(:last-child) {
  margin-bottom: 3rem;
}
.sh .big-img img {
  width: 100%;
  height: auto;
}
.sh .big-img__double {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
  height: 79rem;
}
.sh .big-img__double img {
  width: 100%;
  height: 100%;
}
.sh .collage {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 4rem;
}
.sh .collage:not(:last-child) {
  margin-bottom: 3rem;
}
.sh .collage > div {
  min-height: 72.5rem;
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
.sh .collage p span {
  font-weight: 600;
}
.sh .collage p:not(:last-child) {
  margin-bottom: 1.5rem;
}
.sh .collage__img {
  flex-grow: 1;
}
.sh .collage__img img {
  width: 100%;
  height: 100%;
}
.sh .block:not(:last-child) {
  margin-bottom: 3rem;
}
.sh .block p span {
  font-weight: 600;
}
.sh .block p:not(:last-child) {
  margin-bottom: 1.5rem;
}

.quiz {
  position: relative;
}
.quiz__slide {
  height: 100vh;
  padding: 5rem;
}
.quiz__hero {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  background: url("./../img/quiz.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}
.quiz__hero::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
}
.quiz__title {
  margin: 0;
  margin-bottom: 2rem;
  font-weight: 400;
  font-size: 4.8rem;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--light1000);
}
.quiz__container {
  max-width: calc(84rem + var(--container-offset) * 2);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}
.quiz__logo {
  display: block;
  width: 18rem;
  height: 4rem;
}
.quiz__logo img {
  width: 100%;
  height: 100%;
}
.quiz__right {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}
.quiz__tel {
  font-weight: 400;
  font-size: 2rem;
  text-transform: uppercase;
  color: var(--light1000);
}
@media (any-hover: hover) {
  .quiz__tel:hover {
    text-decoration: underline;
  }
}
.quiz__soc {
  display: block;
  width: 2.4rem;
  height: 2.4rem;
}
.quiz__soc svg {
  width: 100%;
  height: 100%;
}
.quiz__desc {
  margin: 0;
  margin-bottom: 4.8rem;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
  color: var(--light1000);
}
.quiz__content {
  position: relative;
  z-index: 2;
  width: 100%;
  max-width: 100rem;
  text-align: center;
  margin: 0 auto;
}
.quiz__bottom {
  position: relative;
  z-index: 2;
  width: 100%;
}
.quiz__bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.quiz__several {
  margin-bottom: 6rem;
  display: flex;
  align-items: center;
  gap: 0.8rem;
  font-family: var(--second-family);
  font-weight: 600;
  font-size: 1.4rem;
  color: var(--grey1000);
}
.quiz__several svg {
  width: 2.4rem;
  height: 1.8rem;
}
.quiz__quest {
  text-transform: uppercase;
  margin-bottom: 2rem;
  letter-spacing: 0.03em;
}
.quiz__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem 1.5rem;
  max-height: 70vh;
  overflow-y: auto;
}
.quiz__items::-webkit-scrollbar {
  width: 3px;
}
.quiz__items::-webkit-scrollbar-track {
  background: var(--grey400);
}
.quiz__items::-webkit-scrollbar-thumb {
  background: var(--grey1000);
}
.quiz__items--three {
  padding-right: 1rem;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem 3rem;
}
.quiz__label {
  display: flex;
  align-items: center;
  gap: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  padding: 1.7rem 0.7rem;
  cursor: pointer;
}
input[type=radio]:checked + .quiz__radio::after {
  opacity: 1;
}
.quiz__radio {
  position: relative;
  flex-shrink: 0;
  display: block;
  width: 1.8rem;
  height: 1.8rem;
  border-radius: 100%;
  border: 0.5px solid rgba(0, 0, 0, 0.5);
}
.quiz__radio::after {
  content: "";
  position: absolute;
  inset: 0.3rem;
  background-color: var(--grey1000);
  border-radius: 100%;
  opacity: 0;
}
.quiz__answ {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 1.4rem;
  color: var(--grey1000);
}

.quiz-control {
  background-color: var(--light1000);
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 20;
  left: 0;
  bottom: 0;
  width: 100%;
  padding: 2.5rem 0;
  transition: transform 0.4s ease-in;
  transform: translateY(100%);
}
.quiz-control.active {
  transform: translateY(0);
}
.quiz-control__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.quiz-control__buttons {
  display: flex;
  align-items: flex-start;
  gap: 2rem;
}
.quiz-control__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  height: 3.2rem;
  font-family: var(--third-family);
  font-weight: 500;
  font-size: 1.2rem;
  color: var(--light1000);
  text-transform: initial;
  cursor: pointer;
}
.quiz-control__btn--prev {
  width: 3.2rem;
  padding: 0.9rem;
  border: 1px solid rgba(70, 67, 48, 0.5);
  border-radius: 100%;
}
.quiz-control__btn--prev svg {
  width: 100%;
  height: 100%;
}
.quiz-control__btn--next {
  background: var(--grey1000);
  padding: 0.8rem 2.4rem;
}
.quiz-control__btn:not(:last-child) {
  margin-bottom: 0.8rem;
}
.quiz-control__enter {
  font-family: var(--third-family);
  font-weight: 400;
  font-size: 8px;
  color: var(--grey1000);
  text-align: center;
}
.quiz-control__enter span {
  font-weight: 600;
}

.quiz-pag__ready {
  font-family: var(--second-family);
  font-weight: 400;
  font-size: 0.9rem;
  color: #000;
  margin-bottom: 0.8rem;
}
.quiz-pag__ready span {
  font-size: 1.1rem;
}
.quiz-pag__line {
  height: 0.3rem;
  width: 31rem;
  background: var(--grey400);
}

.quiz-item {
  position: relative;
  height: 40rem;
  display: flex;
  flex-direction: column;
  border-radius: 7px;
  overflow: hidden;
  border: 1px solid var(--grey1000);
  cursor: pointer;
}
.quiz-item input[type=radio]:checked + .quiz-item__radio {
  opacity: 1;
}
.quiz-item .quiz-item__radio {
  position: absolute;
  opacity: 0;
  z-index: 3;
  right: 2rem;
  top: 2rem;
  background-color: var(--light1000);
  border: 2px solid var(--grey1000);
}
.quiz-item__img {
  flex-grow: 1;
}
.quiz-item__img img {
  width: 100%;
  height: 100%;
}
.quiz-item__name {
  padding: 1.5rem 1rem;
  background-color: var(--light1000);
}

.quiz-form {
  display: flex;
  align-items: center;
  justify-content: center;
}
.quiz-form__content {
  width: 100%;
  max-width: 56rem;
  margin: 0 auto;
}
.quiz-form__title {
  margin-bottom: 3rem;
}
.quiz-form__label {
  display: block;
}
.quiz-form__label:not(:last-child) {
  margin-bottom: 2rem;
}
.quiz-form__capture {
  display: block;
  margin-bottom: 1rem;
  font-weight: 400;
  font-size: 1.2rem;
  text-transform: uppercase;
  color: var(--grey1000);
}
.quiz-form__wrapper {
  position: relative;
}
.quiz-form__icon {
  position: absolute;
  left: 1.5rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.6rem;
  height: 1.6rem;
}
.quiz-form__icon svg {
  width: 100%;
  height: 100%;
}
.quiz-form__input {
  display: block;
  width: 100%;
  height: 6rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 7px;
  padding: 0px 1.6rem;
  padding-left: 4.2rem;
  background: none;
}
.quiz-form__btn {
  background: var(--grey1000);
  width: 100%;
  height: 6rem;
  text-align: center;
  justify-content: center;
  color: var(--light1000);
}